export default {
	data() {
		return {
			selectIndex: 0,
			buttonList: ['发布动态', '发布活动', '发布招募', '发布摄影', '发布交易'],
		}
	},
	methods: {
		buttonSelect(e) {
			this.selectIndex = e.currentTarget.dataset.id;
			switch (this.selectIndex) {
				case 0:
					uni.navigateTo({
						url: '/pages/addition-article/addition-article'
					});
					break;
			}
		}
	}
}
